import * as actionTypes from "./home-actionType";

const initialState = {
  homeData: [],
};

export default function homeReducer(state = initialState, action) {
  //console.log("homeReducer", action);
  switch (action.type) {
    case actionTypes.FETCH_HOME_DATA:
      return {
        ...state,
        homeData: action.payload.home_data,
      };
    default:
      return state;
  }
}
