import React, { Component } from "react";
import "./Nav.css";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as actionCreator from "../App_State_Mnmt/ActionCreator";

class NavBar extends Component {
  componentDidMount() {
    this.props.action.getHomeData();
  }
  render() {
    return (
      <React.Fragment>
        <nav className="nav">
          <div className="container">
            <div className="logo">{this.props.homeDetails.logoName}</div>

            <div id="mainListDiv" className="main_list">
              <ul className="navlinks">
                <li>
                  <a href="#sectionHome"> Home</a>
                </li>
                <li>
                  <a href="#sectionAbout"> About</a>
                </li>
                <li>
                  <a href="#sectionGallery"> Gallery</a>
                </li>
                <li>
                  <a href="#sectionContact"> Contact</a>
                </li>
              </ul>
            </div>

            <span className="navTrigger">
              <i></i>
              <i></i>
              <i></i>
            </span>
          </div>
        </nav>
        <section id="sectionHome" className="imageHome"></section>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    homeDetails: state.appReducer.homeDetails,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    action: bindActionCreators(actionCreator, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);
