import * as actionTypes from "./ActionType";

const initialState = {
  imageArray: [],
  aboutDetails: {},
  homeDetails: {},
  conatctDetails: {},
  appLoader: true,
};

export default function appReducer(state = initialState, action) {
  switch (action.type) {
    case actionTypes.FETCH_GALLERY_IMAGE:
      return {
        ...state,
        imageArray: action.payload,
      };
    case actionTypes.FETCH_ABOUT_DETAILS:
      return {
        ...state,
        aboutDetails: action.payload,
        loadingFlag: true,
      };
    case actionTypes.FETCH_HOME_DETAILS:
      return {
        ...state,
        homeDetails: action.payload,
      };
    case actionTypes.FETCH_CONTACT_DETAILS:
      return {
        ...state,
        conatctDetails: action.payload,
        appLoader: false,
      };
    case actionTypes.LOADER_APP:
      console.log(action.payload);
      return {
        ...state,
        appLoader: action.payload,
      };
    default:
      return state;
  }
}
