import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as actionCreator from "../App_State_Mnmt/ActionCreator";
//import "../fontAwesome.css";

const skillRating = (value) => {
  let rating = Number(value);
  let ratingRound = Math.floor(rating);
  let array = [];

  for (var a = 1; a <= ratingRound; a++) {
    array.push(<span key={a} className="fa fa-star"></span>);
  }
  if (Math.floor(value) !== rating) {
    array.push(<span key={a + 1} className="fa fa-star-half-full"></span>);
  }
  return array;
};

function AboutPage(props) {
  const { getAboutDetails } = props.action;
  const [LoadingFlag, setLoadingFlag] = useState(false);
  const {
    userName,
    userDescribtion,
    userMobNo,
    userPhoto,
    userEmail,
    userDateOfBirth,
    userLocation,
    programmingLang,
    uiTech,
    userLanguage,
    userHiEducation,
    skillsRating,
    userAward,
  } = props.aboutDetails;

  useEffect(() => {
    getAboutDetails();
    setLoadingFlag(props.loadingFlag);
  }, [props.loadingFlag]);

  return (
    <div className="container" id="sectionAbout">
      {LoadingFlag ? (
        <>
          <div style={{ paddingTop: "20px" }}>
            <h1>About Me</h1>
          </div>
          <div className="row">
            <div className="col-md-3 col-sm-12">
              <img
                src={userPhoto}
                draggable={false}
                alt="user pix"
                style={{
                  border: "1px solid #ddd",
                  borderRadius: "4px",
                  padding: "5px",
                  maxWidth: "100%",
                  maxHeight: "100%",
                  objectFit: "contain",
                }}
              />
            </div>
            <div className="col-md-9 col-sm-12">
              <div
                className="typewriter"
                style={{
                  backgroundColor: "rgb(197, 195, 195)",
                  textAlign: "center",
                  height: "3.2rem",
                  fontFamily: "cursive",
                }}
              >
                <h2 style={{ color: "#191818" }}>{userName}</h2>
              </div>

              <div
                className="mt-2"
                style={{
                  minHeight: "83%",
                  borderRadius: "5px",
                  fontSize: "large",
                  // border: "1px solid #cccbcb",
                  padding: "5px",
                }}
              >
                <div className="row">
                  {skillsRating.map((item, i) => {
                    return (
                      <div key={i} className="col-md-3 col-sm-6 col-6">
                        <div>
                          {item.label}
                          <div>{skillRating(item.rating)}</div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
          {/* row end */}

          <div className="row m-1">
            <div
              className="col-md-12"
              style={{
                border: "1px solid #cccbcb",
                padding: "5px",
                borderRadius: "5px",
              }}
            >
              <div dangerouslySetInnerHTML={{ __html: userDescribtion }} />
            </div>
          </div>

          <div className="row m-1">
            <div
              className="col-md-12"
              style={{
                border: "1px solid #cccbcb",
                padding: "5px",
                borderRadius: "5px",
              }}
            >
              <div className="row">
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Name :</label>{" "}
                  {userName}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Hi.Edu</label>{" "}
                  {userHiEducation}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Email :</label>{" "}
                  {userEmail}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>DOB :</label>{" "}
                  {userDateOfBirth}
                </div>
              </div>

              <div className="row">
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Location :</label>
                  {userLocation}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Programming :</label>
                  {programmingLang}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>UI Tech :</label>
                  {uiTech}
                </div>
                <div className="col-md-3 col-sm-6">
                  <label style={{ fontWeight: "bold" }}>Language :</label>{" "}
                  {userLanguage}
                </div>
              </div>
            </div>
          </div>
          <div className="row m-1">
            <div
              className="col-md-12"
              style={{
                border: "1px solid #cccbcb",
                padding: "5px",
                borderRadius: "5px",
              }}
            >
              <span
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignContent: "center",
                  backgroundColor: "#c5c3c3",
                  borderRadius: "5px",
                  color: "black",
                  fontWeight: "bold",
                }}
              >
                Award
              </span>
              <div className="row">
                {userAward.map((items, i) => {
                  return (
                    <div key={i + 1} className="col-md-6 col-sm-6">
                      <div dangerouslySetInnerHTML={{ __html: items }} />
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
          <hr />
        </>
      ) : (
        <div>Loading....</div>
      )}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    aboutDetails: state.appReducer.aboutDetails,
    loadingFlag: state.appReducer.loadingFlag,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    action: bindActionCreators(actionCreator, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AboutPage);
