import React, { useState } from "react";
import { Modal } from "react-bootstrap";

import "./imageModal.css";

function ImageModal(props) {
  const [showModal, setshowModal] = useState(props.viewFlag);

  function updateFlag() {
    setshowModal(false);
    props.changeFlag();
  }

  return (
    <>
      <Modal
        size="lg"
        show={showModal}
        onHide={updateFlag}
        dialogClassName="modal-m3"
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-lg">
            {props.modalTitle.replace(/_/g, " ")}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {props.imageArray.map((item) => {
            return (
              <img
                src={item}
                draggable={false}
                alt="clickedImage"
                className="responsive"
                style={{ padding: "10px" }}
              ></img>
            );
          })}
        </Modal.Body>
      </Modal>
    </>
  );
}

export default ImageModal;
