import React, { Component } from "react";
import ImageFolder from "./ImageFolder";
import * as actionCreator from "../App_State_Mnmt/ActionCreator";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

class GalleryPage extends Component {
  componentDidMount() {
    this.props.action.getGalleryImage();
  }
  render() {
    return (
      <div
        id="sectionGallery"
        className="container"
        style={{ minHeight: "650px" }}
      >
        <div style={{ marginTop: "50px", paddingTop: "15px" }}> </div>
        <h1>Gallery</h1>
        {Object.keys(this.props.imageArray).map((x, i) => {
          return (
            <React.Fragment key={x}>
              <ImageFolder
                folderName={x}
                imageArray={this.props.imageArray[x]}
              />
            </React.Fragment>
          );
        })}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    imageArray: state.appReducer.imageArray,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    action: bindActionCreators(actionCreator, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(GalleryPage);
