import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as actionCreator from "../App_State_Mnmt/ActionCreator";
import bgImage from "../Resource/contactBg.jpg";
import linkedIn from "../Resource/linkedIn.png";
import facebook from "../Resource/facebook.png";
import instagram from "../Resource/instagram.png";
import twitter from "../Resource/twetter.png";

class ContactPage extends Component {
  componentDidMount() {
    this.props.action.getContactData();
  }
  render() {
    const { conatctDetails } = this.props;
    return (
      <div
        id="sectionContact"
        style={{
          backgroundImage: `URL(${bgImage})`,
          backgroundSize: "100%",
          paddingBottom: "1%",
          minHeight: "700px",
        }}
      >
        <div className="container">
          <h1>Contacts</h1>
          <div
            className="container"
            style={{
              paddingTop: "20px",
              paddingBottom: "20px",
              backgroundColor: "#060a0c8c",
              marginTop: "5%",
              marginBottom: "10%",
            }}
          >
            <h4
              style={{
                color: "white",
                textAlign: "center",
              }}
            >
              {conatctDetails.email_id}
            </h4>
            <hr
              style={{
                textAlign: "center",
                margin: "3%",
                backgroundColor: "white",
                padding: "1px",
              }}
            />
            <div className="row" style={{ margin: "8%" }}>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href={conatctDetails.linkedInLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={linkedIn}
                    draggable={false}
                    className="img-fluid contactSocailNetwork"
                    alt="linkedInIcon"
                  />
                </a>
              </div>

              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href={conatctDetails.facebookLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={facebook}
                    draggable={false}
                    className="img-fluid contactSocailNetwork"
                    alt="facebookIcon"
                  />
                </a>
              </div>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href={conatctDetails.instaLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={instagram}
                    draggable={false}
                    className="img-fluid contactSocailNetwork"
                    alt="InstaIcon"
                  />
                </a>
              </div>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href={conatctDetails.twitterLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={twitter}
                    draggable={false}
                    className="img-fluid contactSocailNetwork"
                    rel="noopener noreferrer"
                    alt="twitterIcon"
                  />
                </a>
              </div>
            </div>
            <hr
              style={{
                textAlign: "center",
                margin: "3%",
                backgroundColor: "white",
                padding: "1px",
              }}
            />
            <h3
              style={{
                color: "white",
                textAlign: "center",
              }}
            >
              {conatctDetails.footerMessage}
            </h3>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    conatctDetails: state.appReducer.conatctDetails,
  };
};

const mapDispactToProps = (dispatch) => {
  return {
    action: bindActionCreators(actionCreator, dispatch),
  };
};

export default connect(mapStateToProps, mapDispactToProps)(ContactPage);
