import * as actionTypes from "./ActionType";
import axios from "axios";

export const formatStateData = (type, data) => {
  return {
    type: type,
    payload: data,
  };
};

export const getHomeData = () => {
  return (dispatch) => {
    let url =
      "https://spreadsheets.google.com/feeds/list/1_YLaCC87yfVXCkyCLGRCNmHQ9XZBdQtGzZvjUbJ7bIs/1/public/values?alt=json";
    return axios
      .get(url)
      .then((res) => {
        const homeData = res.data.feed.entry;
        const objTemp = {};

        for (let a = 0; a < homeData.length; a++) {
          let obj;
          obj = { [homeData[a].gsx$key.$t]: homeData[a].gsx$value.$t };
          Object.assign(objTemp, obj);
        }
        dispatch(formatStateData(actionTypes.FETCH_HOME_DETAILS, objTemp));
        //   console.log(tempObj);
      })
      .catch();
  };
};
export const getAboutDetails = () => {
  return (dispatch) => {
    let url =
      "https://spreadsheets.google.com/feeds/list/1_YLaCC87yfVXCkyCLGRCNmHQ9XZBdQtGzZvjUbJ7bIs/2/public/values?alt=json";
    return axios
      .get(url)
      .then((res) => {
        const aboutData = res.data.feed.entry;
        const temp_obj = {};
        for (let a = 0; a < aboutData.length; a++) {
          let obj;
          if (
            aboutData[a].gsx$key.$t === "userAward" ||
            aboutData[a].gsx$key.$t === "skillsRating"
          ) {
            let temp = JSON.parse(aboutData[a].gsx$value.$t);
            obj = { [aboutData[a].gsx$key.$t]: temp };
          } else {
            obj = { [aboutData[a].gsx$key.$t]: aboutData[a].gsx$value.$t };
          }

          Object.assign(temp_obj, obj);
        }
        dispatch(formatStateData(actionTypes.FETCH_ABOUT_DETAILS, temp_obj));
      })
      .catch();
  };
};

export const getGalleryImage = () => {
  return (dispatch) => {
    let url =
      "https://spreadsheets.google.com/feeds/list/1_YLaCC87yfVXCkyCLGRCNmHQ9XZBdQtGzZvjUbJ7bIs/3/public/values?alt=json";
    return axios
      .get(url)
      .then((res) => {
        const temp_array = res.data.feed.entry;
        const format_array = [];
        const group_name = [];
        let final_objectArray = {};

        for (let a = 0; a < temp_array.length; a++) {
          let obj = {
            folderName: temp_array[a].gsx$groupname.$t,
            imgLink: temp_array[a].gsx$imglink.$t,
          };
          group_name.push(obj.folderName);
          format_array.push(obj);
        }
        const uniqueGrp_name = Array.from(new Set(group_name));
        for (let a = 0; a < uniqueGrp_name.length; a++) {
          const links = [];
          for (let b = 0; b < format_array.length; b++) {
            if (uniqueGrp_name[a] === format_array[b].folderName) {
              links.push(format_array[b].imgLink);
            }
          }

          final_objectArray = Object.assign(
            { ...final_objectArray },
            { [uniqueGrp_name[a]]: links }
          );
        }

        dispatch(
          formatStateData(actionTypes.FETCH_GALLERY_IMAGE, final_objectArray)
        );
      })
      .catch();
  };
};

