import React, { useState, useEffect } from "react";
import defaultImage from "../Resource/defaultGallery.jpg";

import ImageModal from "./ImageModal.js";

const ImageFolder = (props) => {
  const [modalFlag, setModalFlag] = useState(false);
  const [imageArray, setImageArray] = useState([
    defaultImage,
    defaultImage,
    defaultImage,
    defaultImage,
  ]);

  function viewAllImg() {
    setModalFlag(!modalFlag);
  }

  const setFolderIcon = () => {
    let imageCount = props.imageArray.length;
    let temp;
    if (imageCount <= 4) {
      temp = imageArray.splice(0, imageCount, ...props.imageArray);
    } else {
      let [a, b, c, d] = [...props.imageArray];
      temp = imageArray.splice(0, 4, a, b, c, d);
    }

    return temp;
  };

  useEffect(() => {
    let newIconArray = setFolderIcon(imageArray);
    setImageArray([...imageArray, ...newIconArray]);
  }, [setImageArray]);

  return (
    <React.Fragment>
      <div className="imageGalleryDiv" onClick={viewAllImg}>
        <div>
          <span>
            <img
              src={imageArray[0]}
              draggable={false}
              alt="img1"
              className="imageFolder"
            />
          </span>
          <span>
            <img
              src={imageArray[1]}
              draggable={false}
              alt="img2"
              className="imageFolder"
            />
          </span>
        </div>
        <div>
          <span>
            <img
              src={imageArray[2]}
              draggable={false}
              alt="img3"
              className="imageFolder"
            />
          </span>
          <span>
            <img
              src={imageArray[3]}
              draggable={false}
              alt="img4"
              className="imageFolder"
            />
          </span>
        </div>
        <span className="imageFolderTitle">
          {props.folderName.replace(/_/g, " ")}
        </span>
      </div>
      {modalFlag ? (
        <ImageModal
          viewFlag={modalFlag}
          changeFlag={viewAllImg}
          modalTitle={props.folderName}
          imageArray={props.imageArray}
        />
      ) : (
        ""
      )}
    </React.Fragment>
  );
};

export default ImageFolder;
